<template>
    <div class="order-confirm">
        <mooc-mall-header></mooc-mall-header>
        <mooc-mall-bread>CONFIRM</mooc-mall-bread>
        <div class="order-main">
            <div class="container">
                <div class="checkout-title">
                    <h1>CHECK OUT</h1>
                </div>
                <div class="step-div">
                    <ul class="step-ul">
                        <li class="current"><span>Confirm address</span></li>
                        <li class="current"><span>View your order</span></li>
                        <li><span>Make payment</span></li>
                        <li><span>Order confirmation</span></li>
                    </ul>
                </div>
                <div class="order-content-title-wrapper">
                    <h2>ORDER CONTENT</h2>
                </div>
                <div class="order-content-main-wrapper">
                    <div class="order-content-item">
                        <div class="order-head">
                            <ul>
                                <li>Order contents</li>
                                <li>Price</li>
                                <li>Quantity</li>
                                <li>Subtotal</li>
                            </ul>
                        </div>
                        <ul class="order-list">
                            <li v-for="(item, index) in orderListPro" :key="index" v-if="item.checked==1">
                                <div class="order-list-content">
                                    <div class="order-list-content-pic">
                                        <img :key="item.productImage"
                                             v-lazy="'productsImg/'+item.productImage">
                                    </div>
                                    <div class="order-list-content-title">{{item.productName}}</div>
                                </div>
                                <div class="order-list-price">{{item.salePrice | currency('¥')}}</div>
                                <div class="order-list-quantity">
                                    <span>x{{item.productNum}}</span>
                                </div>
                                <div class="order-list-subtotal">{{item.productNum*item.salePrice | currency('¥')}}
                                </div>
                            </li>
                        </ul>
                        <ul class="order-list-response">
                            <li v-for="(item, index) in orderListPro" :key="index" v-if="item.checked==1">
                                <div class="list-top">
                                    <span class="itemImg">
                                         <img :key="item.productImage"
                                              v-lazy="'productsImg/'+item.productImage">
                                    </span>
                                    <span class="item-title">{{item.productName}}</span>
                                </div>
                                <div class="list-bottom">
                                    <span class="item-num">×{{item.productNum}}</span>
                                    <span class="item-total">{{item.productNum*item.salePrice | currency('¥')}}</span>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
                <div class="order-info-main">
                    <ul class="order-info-ul">
                        <li><span>Item Subtotal:</span><span>{{itemSubtotal | currency('¥')}}</span></li>
                        <li><span>Discount:</span><span>{{discount | currency('¥')}}</span></li>
                        <li><span>Tax:</span><span>{{tax | currency('¥')}}</span></li>
                        <li><span>Order Total:</span><span>{{itemSubtotal+discount+tax | currency('¥')}}</span></li>
                    </ul>
                </div>
                <div class="order-method">
                    <span @click="gobackAdd" class="btn">previous</span>
                    <!--<span @click="gobackAdd" class="btn-previous">previous</span>-->
                    <span @click="payMent" class="btn btn--red">proceed to payment</span>
                    <!--<span @click="payMent" class="btn-payment">proceed to payment</span>-->
                </div>
            </div>
        </div>
        <mooc-mall-footer></mooc-mall-footer>
    </div>
</template>

<script>
    import axios from 'axios'
    import MoocMallHeader from '@/components/MoocMallHeader'
    import MoocMallBread from '@/components/MoocMallBread'
    import MoocMallFooter from '@/components/MoocMallFooter'

    export default {
        name: 'OrderConfirm',
        data () {
            return {
                orderListPro: [],
                discount: 10,
                tax: 80,
                itemSubtotal: 0
            }
        },
        components: {
            MoocMallHeader,
            MoocMallBread,
            MoocMallFooter
        },
        mounted () {
            this.initCheckedPro()
        },
        methods: {
            initCheckedPro () {
                axios.get('/users/cartList').then(response => {
                    let res = response.data
                    if (res.status == '0') {
                        this.orderListPro = res.result
                        this.orderListPro.forEach(item => {
                            if (item.checked == '1') {
                                this.itemSubtotal += parseFloat(item.salePrice * item.productNum)
                            }
                        })
                    }
                })
            },
            gobackAdd () {
                this.$router.push('/Address')
            },
            payMent () {
                let addressId = this.$route.params.id
                axios.post('/users/parMent', {
                    addressId: addressId,
                    orderTotal: parseFloat(this.itemSubtotal + this.discount + this.tax)
                }).then(response => {
                    let res = response.data
                    if (res.status == '0') {
                        this.$router.push({
                            path: `/orderSuccess/${res.result.orderId}/${res.result.orderTotal}`
                        })
                    }
                })
            }
        }
    }
</script>

<style lang="scss" scoped>
    .order-confirm {
        .order-main {
            margin-top: 40px;

            .container {
                .checkout-title {
                    h1 {
                        font-weight: 700;
                        letter-spacing: 5px;
                        color: #000;
                    }
                }

                .step-div {
                    margin-top: 15px;

                    .step-ul {
                        /*border: 1px solid #d1434a;*/
                        display: flex;
                        flex-wrap: nowrap;
                        justify-content: space-between;

                        li {
                            line-height: 55px;
                            text-align: center;
                            border-bottom: 3px solid #ccc;
                            width: 25%;
                            position: relative;
                            text-transform: capitalize;

                            &::after {
                                width: 14px;
                                height: 14px;
                                content: '';
                                -webkit-border-radius: 50%;
                                -moz-border-radius: 50%;
                                border-radius: 50%;
                                position: absolute;
                                bottom: -7px;
                                left: 50%;
                                margin-left: -7px;
                                background-color: #ccc;
                            }

                            span {
                                font-weight: 700;
                                font-size: 16px;
                                color: #ccc;
                                /*white-space: nowrap;*/
                            }
                        }

                        .current {
                            border-color: #d1434a;

                            span {
                                color: #d1434a;
                            }

                            &::after {
                                background-color: #d1434a;
                            }
                        }
                    }
                }

                .order-content-title-wrapper {
                    margin-top: 30px;

                    h2 {
                        font-size: 16px;
                        font-weight: 600;
                        letter-spacing: 4px;
                        color: #666;
                    }
                }

                .order-content-main-wrapper {
                    margin: 25px 0;

                    .order-content-item {
                        display: table;
                        width: 100%;

                        .order-head {
                            display: table-header-group;
                            width: 100%;

                            ul {
                                display: table-row;
                                width: 100%;

                                li {
                                    display: table-cell;
                                    height: 40px;
                                    line-height: 40px;
                                    background-color: #605f5f;
                                    color: #ffffff;
                                    text-align: center;
                                    text-transform: uppercase;
                                    font-size: 15px;
                                    letter-spacing: 3px;
                                }
                            }
                        }

                        .order-list {
                            display: table-row-group;

                            li {
                                display: table-row;
                                background-color: #ffffff;
                                /*border-bottom: 1px solid #e9e9e9;*/
                                .order-list-content {
                                    min-height: 72px;
                                    padding: 32px;
                                    border-left: 1px solid #e9e9e9;

                                    .order-list-content-pic {
                                        width: 80px;
                                        height: 80px;
                                        border: 1px solid #e9e9e9;
                                        overflow: hidden;

                                        img {
                                            width: 100%;
                                            height: 100%;
                                        }
                                    }

                                    .order-list-content-title {
                                        border: none;
                                        padding: 0 20px;
                                        font-size: 14px;
                                        color: #000;
                                        font-weight: 700;
                                    }
                                }

                                .order-list-price {
                                    font-size: 16px;
                                    color: #000;
                                    font-weight: 500;
                                }

                                .order-list-quantity {
                                    span {
                                        display: inline-block;
                                        width: 50px;
                                        height: 30px;
                                        text-align: center;
                                        line-height: 30px;
                                        font-size: 16px;
                                        letter-spacing: 2px;
                                        border: 1px solid #e9e9e9;
                                    }
                                }

                                .order-list-subtotal {
                                    border-right: 1px solid #e9e9e9;
                                    color: #D1434A;
                                    font-size: 17px;
                                    font-weight: 500;
                                }

                                div {
                                    display: table-cell;
                                    text-align: center;
                                    vertical-align: middle;
                                    border-bottom: 1px solid #e9e9e9;
                                    height: 100%;
                                }
                            }
                        }
                        .order-list-response {
                            display: none;
                        }
                    }
                }

                .order-info-main {
                    text-align: right;
                    font-size: 20px;

                    .order-info-ul {
                        display: table;
                        width: 100%;

                        li {
                            display: table-row;

                            span {
                                display: table-cell;
                                text-align: right;
                                height: 40px;
                                padding-right: 20px;
                            }

                            span:first-child {
                                color: #999;
                            }

                            span:last-child {
                                width: 150px;
                                padding-left: 10px;
                                white-space: nowrap;
                            }
                        }

                        li:last-child {
                            span:last-child {
                                color: #D1434A;
                                font-size: 22px;
                                font-weight: 700;
                            }
                        }
                    }
                }

                .order-method {
                    margin: 25px 0 0 0;
                    display: flex;
                    justify-content: space-between;
                    flex-wrap: wrap;
                }
            }
        }
    }
</style>
